body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-page-container {
  text-align: center;
  padding-top: 5%; }

* {
  box-sizing: border-box;
  color: #eee; }

.btn {
  background: #cf33c7;
  padding: 20px;
  text-align: center;
  border: none; }
  .btn:hover {
    background: #d95cd2;
    cursor: pointer; }

.btn-center {
  margin: 10px auto;
  display: 'block'; }

.layout-container {
  background: #404040; }

.middle-page-container {
  background: #404040;
  min-height: calc(100vh - 61px); }

header {
  background: #4d4d4d;
  color: #eee;
  overflow: hidden; }
  header .logo {
    float: left;
    margin-left: 10px;
    padding: 20px 20px;
    background: #333333;
    color: #eee;
    outline: none;
    height: 100%;
    border: none;
    font-size: 1em;
    font-family: inherit; }
    header .logo:hover {
      box-shadow: 5px 1px 2px #222222;
      background: #4d4d4d; }
  header .header-tabs {
    float: right;
    margin-right: 5px; }
    header .header-tabs a {
      display: inline-block;
      text-decoration: none;
      padding: 20px;
      background: #333333;
      color: #eee;
      margin: 0 5px; }
      header .header-tabs a:hover {
        box-shadow: 5px 1px 2px #222222;
        background: #4d4d4d;
        color: #eee; }

.personaldetails-container {
  padding-top: 5%;
  text-align: center; }
  .personaldetails-container .personaldetails-container-form-container {
    width: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(6, 1fr); }
    .personaldetails-container .personaldetails-container-form-container label {
      grid-column: 1/span 2; }
    .personaldetails-container .personaldetails-container-form-container input[type="text"] {
      grid-column: 3/span 4;
      padding: 15px;
      color: #333;
      font-size: 1.2em; }
    .personaldetails-container .personaldetails-container-form-container button {
      grid-column: 4;
      margin-left: 0;
      width: 100%; }

.game-page-container {
  min-height: calc(100vh - 61px);
  overflow: hidden;
  position: relative; }
  .game-page-container .score-board-container {
    width: 25%;
    margin-top: 10%;
    float: right;
    background: #4d4d4d;
    border: 1px solid orange; }
    .game-page-container .score-board-container .score-card-header {
      width: 100%;
      padding: 10px;
      background: #ddd;
      cursor: pointer;
      color: #333;
      text-align: Center;
      font-weight: bold; }
      .game-page-container .score-board-container .score-card-header:hover {
        background: #c4c4c4; }
    .game-page-container .score-board-container .score-card {
      padding-top: 20%;
      padding-bottom: 5%; }
      .game-page-container .score-board-container .score-card-player-name-header {
        color: #3399ff;
        padding-left: 5%;
        font-size: 1.8em; }
      .game-page-container .score-board-container .score-card-player-score {
        padding-left: 5%; }
    .game-page-container .score-board-container .match-stats-card {
      padding-bottom: 5%; }
      .game-page-container .score-board-container .match-stats-card .match-stats-header {
        color: #3399ff;
        padding-left: 5%;
        font-size: 1.8em;
        margin-bottom: 5px; }
      .game-page-container .score-board-container .match-stats-card .match-stats-content {
        padding-left: 5%; }
  .game-page-container .game-board-container {
    width: 20%;
    margin: 0 auto;
    padding-top: 10%;
    background: #4d4d4d; }
    .game-page-container .game-board-container .player-header {
      text-align: center; }
    .game-page-container .game-board-container .game-board-btn-container {
      display: grid;
      grid-template-columns: auto auto;
      padding: 0 2%;
      grid-gap: 5px; }
    .game-page-container .game-board-container .congrats-message-container {
      text-align: center;
      padding: 10px;
      margin-bottom: 10px; }
      .game-page-container .game-board-container .congrats-message-container h2, .game-page-container .game-board-container .congrats-message-container p {
        color: #33adff; }
    .game-page-container .game-board-container .draw-message-container {
      text-align: center;
      padding: 10px;
      margin-bottom: 10px; }
      .game-page-container .game-board-container .draw-message-container h2, .game-page-container .game-board-container .draw-message-container h4, .game-page-container .game-board-container .draw-message-container p {
        color: #ffff00; }
    .game-page-container .game-board-container .tictac-board-component {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      margin-bottom: 10px; }
      .game-page-container .game-board-container .tictac-board-component .tictac-cell {
        width: 90%;
        margin: 5px auto;
        background: rgba(117, 163, 163, 0.8);
        cursor: pointer;
        height: 60px;
        position: relative; }
        .game-page-container .game-board-container .tictac-board-component .tictac-cell .default {
          width: 100%;
          height: 100%; }
          .game-page-container .game-board-container .tictac-board-component .tictac-cell .default:hover {
            background: rgba(148, 183, 183, 0.8); }
        .game-page-container .game-board-container .tictac-board-component .tictac-cell .red-cross:hover {
          background: rgba(148, 183, 183, 0.8); }
        .game-page-container .game-board-container .tictac-board-component .tictac-cell .red-cross:before {
          content: "";
          position: absolute;
          height: 2px;
          width: 60px;
          top: 50%;
          background: #ff704d;
          -webkit-transform: translateY(1px) rotate(-45deg);
                  transform: translateY(1px) rotate(-45deg); }
        .game-page-container .game-board-container .tictac-board-component .tictac-cell .red-cross:after {
          content: "";
          position: absolute;
          height: 60px;
          width: 2px;
          left: 50%;
          background: #ff704d;
          -webkit-transform: translateX(1px) rotate(-45deg);
                  transform: translateX(1px) rotate(-45deg); }
        .game-page-container .game-board-container .tictac-board-component .tictac-cell .green-dot {
          top: 50%;
          left: 50%;
          width: 100%;
          height: 100%;
          position: absolute;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          border-radius: 50%;
          background: #357a35; }
          .game-page-container .game-board-container .tictac-board-component .tictac-cell .green-dot:hover {
            background: #75b175; }
  .game-page-container #toggle-history-page-btn {
    position: absolute;
    left: 0;
    top: 20%;
    padding: 10px;
    background: #eee;
    cursor: pointer;
    transition: left 0.5s; }
    .game-page-container #toggle-history-page-btn:hover {
      background: white; }
  .game-page-container .closed-sign, .game-page-container .open-sign {
    width: 10px;
    height: 10px;
    border-top: 1px solid black; }
  .game-page-container .open-sign {
    border-right: 1px solid black;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .game-page-container .closed-sign {
    border-left: 1px solid black;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .game-page-container .history-page-container {
    position: absolute;
    top: 20%;
    left: 0;
    height: 80%;
    background: #eee;
    opacity: 0.8;
    transition: width 0.5s;
    z-index: 1; }
    .game-page-container .history-page-container .history-page-content {
      display: flex;
      flex-direction: column;
      width: 100%;
      min-height: 100%;
      padding: 20px;
      overflow: auto; }
      .game-page-container .history-page-container .history-page-content .row {
        padding: 10px;
        color: #ddd;
        display: flex;
        margin-bottom: 10px;
        justify-content: space-between;
        color: #333;
        height: auto; }
        .game-page-container .history-page-container .history-page-content .row .history-content-sno-col {
          flex-basis: 20%;
          padding: 10px;
          color: #333;
          border-bottom: 1px solid #333;
          text-align: center;
          font-size: 1.2em; }
          .game-page-container .history-page-container .history-page-content .row .history-content-sno-col strong {
            color: #333; }
        .game-page-container .history-page-container .history-page-content .row .history-content-result-col, .game-page-container .history-page-container .history-page-content .row .history-content-winner-col {
          flex-basis: 30%;
          padding: 10px;
          color: #333;
          flex-grow: auto;
          border-bottom: 1px solid #333;
          text-align: center; }
          .game-page-container .history-page-container .history-page-content .row .history-content-result-col strong, .game-page-container .history-page-container .history-page-content .row .history-content-winner-col strong {
            color: #333; }

